import React from 'react'
import { Link, useLocation } from 'react-router-dom'

const Navbar = () => {
const location = useLocation();
  return (
       <nav className="navbar navbar-expand-lg bg-dark navbar-dark sticky-top">
                <div className="container">
                    <Link className="navbar-brand" to="/" style={{fontFamily:"Times New Roman", letterSpacing: "1px"}}>News App</Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse justify-content-end" id="navbarSupportedContent">
                        <ul className="navbar-nav mb-2 mb-lg-0 txt">
                            <li className="nav-item">
                                <Link className={`nav-link mx-2 hvr-underline-from-left text-capitalize ${location.pathname==="/"?"active":""}`} aria-current="page" to="/">Home</Link>
                            </li>
                          
                            <li className="nav-item">
                                <Link className={`nav-link mx-2 hvr-underline-from-left text-capitalize ${location.pathname==="/business"?"active":""}`} to="/business">business</Link>
                            </li>
                            <li className="nav-item">
                                <Link className={`nav-link mx-2 hvr-underline-from-left text-capitalize ${location.pathname==="/entertainment"?"active":""}`} to="/entertainment">entertainment</Link>
                            </li>
                            <li className="nav-item">
                                <Link className={`nav-link mx-2 hvr-underline-from-left text-capitalize ${location.pathname==="/general"?"active":""}`} to="/general">general</Link>
                            </li>
                            <li className="nav-item">
                                <Link className={`nav-link mx-2 hvr-underline-from-left text-capitalize ${location.pathname==="/health"?"active":""}`} to="/health">health</Link>
                            </li>
                            <li className="nav-item">
                                <Link className={`nav-link mx-2 hvr-underline-from-left text-capitalize ${location.pathname==="/science"?"active":""}`} to="/science">science</Link>
                            </li>
                            <li className="nav-item">
                                <Link className={`nav-link mx-2 hvr-underline-from-left text-capitalize ${location.pathname==="/sports"?"active":""}`} to="/sports">sports</Link>
                            </li>
                            <li className="nav-item">
                                <Link className={`nav-link mx-2 hvr-underline-from-left text-capitalize ${location.pathname==="/technology"?"active":""}`} to="/technology">technology</Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
  )
}

export default Navbar
