import React from 'react'
import Aos from 'aos';
import { useEffect, useState } from 'react';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import LoadingBar from 'react-top-loading-bar'
import Navbar from './components/Navbar'
import News from './components/News'
import NoPage from './components/NoPage'


const App = () => {

  const apikey = process.env.REACT_APP_NEWS_API;
  const [progress_val, setProgress_val] = useState(0);

  useEffect(() => {
    Aos.init()
  }, []);

  const setProgress = (value) => {
    setProgress_val(value)
  }

  return (

    <>
      <BrowserRouter>
      <Navbar />
      <LoadingBar color='#f11946' height="2.5px"
        // loaderSpeed= "500" 
        progress={progress_val} />
      <Routes>
        <Route path="/" element={<News setProgress={setProgress} pagesize={12} country="in" api={apikey} />} />
        <Route path="/business" element={<News setProgress={setProgress} key="business" pagesize={12} country="in" category='business' api={apikey} />} />
        <Route exact path="/entertainment" element={<News setProgress={setProgress} key="entertainment" pagesize={12} country="in" category='entertainment' api={apikey} />} />
        <Route exact path="/general" element={<News setProgress={setProgress} key="general" pagesize={12} country="in" category='general' api={apikey} />} />
        <Route exact path="/health" element={<News setProgress={setProgress} key="health" pagesize={12} country="in" category='health' api={apikey} />} />
        <Route exact path="/science" element={<News setProgress={setProgress} key="science" pagesize={12} country="in" category='science' api={apikey} />} />
        <Route exact path="/sports" element={<News setProgress={setProgress} key="sports" pagesize={12} country="in" category='sports' api={apikey} />} />
        <Route exact path="/technology" element={<News setProgress={setProgress} key="technology" pagesize={12} country="in" category='technology' api={apikey} />} />
        <Route exact path="*" element={<NoPage />} />
      </Routes>
      </BrowserRouter>
    </>
  )
}

export default App
