import React from 'react'
import NewsItem from './NewsItem'
import Spinner from './Spinner'
import PropTypes from 'prop-types';
import { useEffect, useState } from 'react';
import NoInternet from '../noInternet.gif'


const News = (props) => {

  const [articles, setAticles] = useState([]);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(1);
  const [totalResults, setTotalResults] = useState(0);
  const [IsOnline, setIsOnline] = useState(navigator.onLine);

  const capitalizeString = (string) => {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }
  document.title = `${capitalizeString(props.category)} - NewsApp`

  useEffect(() => {
    update();

    const onlineHandeler = () => {
      setIsOnline(true)
    }

    const offlineHandeler = () => {
      setIsOnline(false)
    }
    window.addEventListener("online", onlineHandeler)
    window.addEventListener("offline", offlineHandeler)

    return () => {
      window.removeEventListener("online", onlineHandeler)
      window.removeEventListener("offline", offlineHandeler)
    };
  }, [page]);

  const update = async () => {
    // console.log('page update no:',page);
    props.setProgress(10)
    try {
      let url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.api}&page=${page}&pagesize=${props.pagesize}`
      setLoading(true)
      props.setProgress(40)
      let data = await fetch(url)
      props.setProgress(70)
      let parsedata = await data.json()
      props.setProgress(90)
      setAticles(parsedata.articles)
      setTotalResults(parsedata.totalResults)
      setLoading(false)
      props.setProgress(100)
    } catch(error) {
      props.setProgress(0)
      console.error("Error:", error);
      console.log("Error:", error);
    }
  }

  const handelPrevClick = () => {
    setPage(page - 1)
    // console.log('page prev no:', page);
    update()
  }

  const handelNextClick = () => {
    setPage(page + 1)
    // console.log('page next no:', page);
    update()
  }

  return (
    <>
      {IsOnline ? (
        <div className='container my-5'>

          <div className="d-flex justify-content-between my-4 txt">
            <button type="button" disabled={page <= 1} className='btn btn-dark btn-sm px-3 rounded-0 text-light hvr-underline-from-right txt' onClick={handelPrevClick}><i className="bi bi-arrow-left"></i> Prev</button>
            <h4 className='text-center' style={{ fontFamily: "Rockwell", letterSpacing: "1px" }}>News App : Top {capitalizeString(props.category)} Headlines</h4>
            <button type="button" disabled={page + 1 > Math.ceil(totalResults / props.pagesize)} className='btn btn-dark btn-sm px-3 rounded-0 text-light hvr-underline-from-left txt' onClick={handelNextClick}>Next <i className="bi bi-arrow-right" ></i></button>
          </div>
          <hr />

          {loading && <Spinner />}
          <div className="row d-flex">
            {!loading && articles.map((item, index) => {
              return (
                <div className="col-lg-3 col-md-4 col-sm-6" key={index}>
                  <NewsItem imgurl={item.urlToImage ? item.urlToImage : "https://static.tnn.in/thumb/msid-100495744,updatedat-1685002664491,width-1280,height-720,resizemode-75/100495744.jpg"} title={item.title ? item.title.slice(0, 40) : "Title"} desc={item.description ? item.description.slice(0, 60) : "DESC"} newsurl={item.url} date={new Date(item.publishedAt).toUTCString().slice(0, 22)} />
                </div>
              )
            })}
          </div>

          {!loading && <div className="d-flex justify-content-between my-5 txt" >
            <button type="button" disabled={page <= 1} className='btn btn-dark btn px-3 rounded-0 text-light hvr-underline-from-right txt' onClick={handelPrevClick}><i className="bi bi-arrow-left"></i> Prev</button>
            <button type="button" disabled={page + 1 > Math.ceil(totalResults / props.pagesize)} className='btn btn-dark btn px-3 rounded-0 text-light hvr-underline-from-left txt' onClick={handelNextClick}>Next <i className="bi bi-arrow-right" ></i></button>
          </div>}
        </div>
        // <p>You are online.</p>
      ) : (
        <div>
          <img src={NoInternet} alt="" />
        </div>
      )}
    </>
  )
}

News.defaultProps = {
  pagesize: 8,
  country: "in",
  category: 'general'

}
News.propTypes = {
  pagesize: PropTypes.number,
  country: PropTypes.string,
  category: PropTypes.string
}

export default News
