import React from 'react'

const NewsItem = (props) => {
  const { imgurl, title, desc, newsurl, date } = props;
  return (
    <div className="card mt-5 mx-2 shadow-sm" data-aos-duration="1000" data-aos="zoom-in">
      <img src={imgurl} className="card-img-top" alt="..." />
      <div className="card-body">
        <h5 className="card-title fw-semibold txt2">{title}</h5>
        <p className="card-text txt2">{desc}</p>
        <p className='txt2'>{date}</p>
        <a href={newsurl} target='_blank'  rel="noreferrer" className="btn btn-dark rounded-0 text-end btn-sm txt" style={{fontFamily:"cambria"}}>Read More</a>
      </div>
    </div>
  )
}

export default NewsItem
